import React, { createContext, useReducer } from "react";
import UserReducer from "./reducers";

const AuthContext = createContext();

const AuthProvider = ({ children }) => {
  const [state, dispatch] = useReducer(UserReducer, {
    user: { name: "Annonymous", authenticated: false },
    posts: [
      {
        title: "Go or Python for backend?",
        content:
          "Well, it depends on what you are trying to build, and what you need to achieve.",
        created_at: new Date().toDateString(),
      },
    ],
  });

  return (
    <AuthContext.Provider value={{ state, dispatch }}>
      {children}
    </AuthContext.Provider>
  );
};

function useAuthUser() {
  const context = React.useContext(AuthContext);
  if (context === undefined) {
    throw new Error("useAuthUser must be used within AuthProvider");
  }
  return context;
}

export { useAuthUser, AuthProvider };
