import {
  Box,
  Flex,
  Spacer,
  Text,
  Drawer,
  Menu,
  MenuList,
  MenuItem,
  DrawerHeader,
  Button,
  DrawerContent,
  useDisclosure,
  DrawerOverlay,
  DrawerCloseButton,
  DrawerBody,
  DrawerFooter,
  List,
  ListItem,
  HStack,
  Avatar,
  MenuButton,
  VStack,
} from "@chakra-ui/react";
import { Link, useNavigate } from "react-router-dom";
// import { HamburgerIcon, ChatIcon } from '@chakra-ui/icons'
import React from "react";
import { useAuthUser } from "../context";
// import { ColorModeSwitcher } from '../ColorModeSwitcher'

export const Navbar = () => {
  const { state, dispatch } = useAuthUser();
  const navigate = useNavigate();

  const Logout = () => {
    console.log("Logging Out");
    dispatch({
      type: "ADD_USER",
      payload: { authenticated: false, name: "Anonymous User" },
    });
    localStorage.removeItem("access_token");
    navigate("/signin");
  };

  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnref = React.useRef();

  return (
    <>
      <Box p={2} boxShadow={"md"}>
        <Flex>
          <Box>
            <Button onClick={onOpen} display={{ md: "none" }} p={0}>
              {/* <HamburgerIcon role={'button'} onClick={onOpen}  boxSize={6} m={2} /> HamburgerIcon */}{" "}
              =
            </Button>

            <Text
              display={{ sm: "none" }}
              ml={2}
              mr={3}
              fontWeight={"bold"}
              fontSize={"2xl"}
            >
              Navbar
            </Text>
          </Box>
          <Spacer width={3} />
          <HStack
            display={{ md: "flex", sm: "none" }}
            justifyContent={"center"}
            alignItems="center"
          >
            <Link to={"/"}>Home</Link>
            {state.user.authenticated ? (
              <></>
            ) : (
              <>
                <Link to={"/signin"}>SignIn</Link>
                <Link to={"/signup"}>SignUp</Link>
              </>
            )}
          </HStack>
          <Spacer />
          {/* <ColorModeSwitcher /> */}
          {state.user.authenticated ? (
            <Menu placement={"bottom-start"}>
              <Avatar as={MenuButton} m={2} boxSize={8} />
              <MenuList>
                <MenuItem>{state.user.name} | {state.user.email}</MenuItem>
                <MenuItem>
                  <Button variant={"unstyled"} onClick={Logout}>
                    Logout
                  </Button>
                </MenuItem>
              </MenuList>
            </Menu>
          ) : (
            ""
          )}
        </Flex>
      </Box>
      <Drawer
        placement="left"
        finalFocusRef={btnref}
        isOpen={isOpen}
        onClose={onClose}
      >
        <DrawerOverlay />
        <DrawerContent>
          <DrawerCloseButton />
          <DrawerHeader textAlign={"center"}>Navbar</DrawerHeader>
          <DrawerBody width={"full"}>
            <VStack>
              <Link onClick={onClose} to={"/"}>
                Home
              </Link>
              <Link onClick={onClose} to={"/signin"}>
                SignIn
              </Link>
              <Link onClick={onClose} to={"/signup"}>
                SignUp
              </Link>
            </VStack>
          </DrawerBody>
          <DrawerFooter>
            <Button m={2} variant={"outline"} colorScheme={"green"}>
              SignIn
            </Button>
            <Button m={2} colorScheme={"green"}>
              SignUp
            </Button>
          </DrawerFooter>
        </DrawerContent>
      </Drawer>
    </>
  );
};
