import React from "react";
import { useNavigate } from "react-router-dom";
import {
  Box,
  Text,
  Flex,
  Button,
  Heading,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Grid,
  GridItem,
} from "@chakra-ui/react";
import { useAuthUser } from "../context";
import { PostForm } from "./PostForm";

export const Home = () => {
  const navigate = useNavigate();
  const user_token = localStorage.getItem("access_token");
  user_token === null && navigate("/signin");
  const { state, dispatch } = useAuthUser();
  !state.user.authenticated && navigate("/signin");

  const fetchPosts = async () => {
    try {
      const res = await fetch("/api/posts", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${user_token}`,
        },
      });
      const data = await res.json();
      dispatch({
        type: "ADD_POSTS",
        payload: data,
      });
    } catch (error) {
      console.log(error);
    }
  };

  const fetchUser = async () => {
    try {
      const res = await fetch("/api/user", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${user_token}`,
        },
      });
      const data = await res.json();
      if (res.status === 401) {
        dispatch({
          type: "ADD_USER",
          payload: { name: "Anonymous User", authenticated: false },
        });
        navigate("/signin");
      }
      if (res.status == 200) {
        dispatch({
          type: "ADD_USER",
          payload: { ...data, authenticated: true },
        });
        return;
      }
    } catch (error) {
      console.log(error);
    }
  };
  React.useEffect(() => {
    user_token !== null && fetchUser();
    fetchPosts();
  }, []);

  return (
    <>
      {state.user.authenticated && (
        <Box pt="3rem" backgroundColor={"whitesmoke"}>
          <Flex
            justifyContent="center"
            alignItems="center"
            backgroundColor={"white"}
            m={2}
            pt={5}
            pb={5}
          >
            <Box>
              <Heading>Welcome Back {state.user.name}</Heading>
              <Text textAlign={"center"} fontSize={"20px"}>
                Hello world lorem
              </Text>
              <Flex>
                <Button
                  _hover={{ backgroundColor: "green.300" }}
                  backgroundColor={"green.500"}
                  color="white"
                  variant={"solid"}
                >
                  Get Started
                </Button>
              </Flex>
            </Box>
          </Flex>
          <PostForm />
          <Box>
            <Heading
              background={"whitesmoke"}
              p="2"
              textAlign={"center"}
              fontSize={18}
            >
              Recent Posts
            </Heading>

            {state.user.authenticated ? (
              <Grid noOfLines={4}>
                {state.posts.map((post) => (
                  <GridItem key={post.title} gap={4}>
                    <Card width={"l"} m={3} backgroundColor={"white"}>
                      <CardHeader>
                        <Flex justifyContent={"space-between"}>
                          <Heading fontSize={16}>{post.title}</Heading>
                          <Button variant={"link"} padding={0}>
                            <Heading fontSize={16}>:</Heading>
                          </Button>
                        </Flex>
                      </CardHeader>
                      <CardBody>
                        <Text>{post.content}</Text>
                      </CardBody>
                      <CardFooter>Posted : {post.created_at}</CardFooter>
                    </Card>
                  </GridItem>
                ))}
              </Grid>
            ) : (
              <Text>Signin To View Posts</Text>
            )}
          </Box>
        </Box>
      )}
    </>
  );
};
