import React from "react";
import {
  Flex,
  Button,
  FormLabel,
  Textarea,
  Heading,
  Input,
  HStack,
  VStack,
  Text,
} from "@chakra-ui/react";
import { useAuthUser } from "../context";

export const PostForm = () => {
  const user_token = localStorage.getItem("access_token");

  const [error, setError] = React.useState("");
  const { state, dispatch } = useAuthUser();

  const [title, setTitle] = React.useState("");
  const [content, setContent] = React.useState("");

  const submitPost = (e) => {
    setError(null);
    e.preventDefault();
    if (title === "" || content === "") {
      setError("Fill all the fields");
      return;
    }
    fetch("/api/post", {
      method: "POST",
      body: JSON.stringify({ title, content }),
      headers: {
        Authorization: `Bearer ${user_token}`,
      },
    })
      .then((res) => {
        res
          .json()
          .then((data) => {
            dispatch({
              type: "ADD_POST",
              payload: data,
            });
          })
          .catch((e) => console.log(e));
      })
      .catch((e) => console.log(e));
    setContent("");
    setTitle("");
  };
  return (
    <VStack boxShadow={"lg"} pt={3} pb={3} backgroundColor={"white"}>
      <Text color={"orange.400"}>{error}</Text>
      <HStack p={2}>
        <FormLabel fontSize="sm" fontWeight="normal">
          Title
        </FormLabel>
        <Input
          borderRadius="15px"
          mb="24px"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          fontSize="sm"
          type="text"
          placeholder="Post Title"
          size="sm"
        />
        <FormLabel fontSize="sm" fontWeight="normal">
          Content
        </FormLabel>
        <Textarea
          rows={3}
          cols={40}
          borderRadius="15px"
          onChange={(e) => setContent(e.target.value)}
          value={content}
          fontSize="md"
          placeholder="Your content goes here..."
          size="sm"
        />
      </HStack>
      <Flex>
        <Button
          onClick={submitPost}
          variant={"solid"}
          _hover={{ backgroundColor: "telegram.300" }}
          backgroundColor={"telegram.400"}
        >
          Post
        </Button>
      </Flex>
    </VStack>
  );
};
